@import '../../../../variables';

.message {
  position: relative;
  &__container {
   
  }

  &__content {
    margin-top: 8px;
  }

  &__bubble {
    display: inline-block;
    max-width: 396px;
    background-color: $messageBcgColor;
    border-radius: 25px 25px 25px 4px;
  }

  &__text {
    padding: 12px 16px;
    font-size: 14px;
    line-height: 140%;
    color: $primaryColor;
  }

  &__date {
    font-size: 10px;
    line-height: 140%;
    color: $secondaryColor;
  }

  &--myMessage {
    display: flex;
    justify-content: flex-end;
    .message__bubble {
      background: $myMessageBcgColor;
      border-radius: 25px 25px 4px 25px;
    }
    .message__content {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
    .message__date {
      position: relative;
    }
    .message__checkedImg {
      display: inline-block;
      position: absolute;
      top: 2px;
      left: -16px;
      width: 14px;
    }
    // &__notReadedImg {
    //   display: inline-block;
    //   position: absolute;
    //   top: 2px;
    //   left: -16px;
    //   width: 14px;
    // }
  }
}
